import { Pipe, PipeTransform } from '@angular/core';
import { IProduct } from '../models/product';

@Pipe({
  name: 'productFilter'
})
export class ProductFilterPipe implements PipeTransform {

  transform(products : IProduct [], filterPattern : string) : IProduct[]{
    // string1.indexOf(string2) returns -1 if string1 does not contain string2
    filterPattern = filterPattern ? filterPattern.toLocaleLowerCase() : null;
    return filterPattern ? products.filter((product : IProduct) => product.name.toLocaleLowerCase().indexOf(filterPattern) !== -1) : products;
  }

}
